/* UI elements "Buttons" */
.button-primary {
    position: relative;
    display: block;
    padding: 1.25em 1.75em;
    border: none;
    border-radius: 999px;
    background-color: #787d46;
    overflow: hidden;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    text-decoration: none;
    color: #ffffff;
    transition: background-color 0.5s ease 0s;
}

@media only screen and (min-width: 768px) {
    .button-primary {
        font-size: 16px;
        line-height: 20px;
    }
}

.button-primary>span {
    position: relative;
}

.button-primary::before {
    content: "";
    position: absolute;
    top: 0%;
    left: -100%;
    height: 100%;
    width: 100%;
    background-image: url("../images/bg/bg-button.svg");
    background-repeat: repeat-x;
    background-position: center;
    background-size: contain;
    transition: left 0.5s ease 0s;
}

.button-primary:hover,
.button-primary:focus-within {
    background-color: #7a7f48;
}

.button-primary:hover::before,
.button-primary:focus-within::before {
    left: 0%;
}

